import { Component, Input, ViewEncapsulation } from '@angular/core';

import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType } from 'shared-code-lib';
import { Component as DWFComponent, IManifest, Manifest } from '../../../blocks/models/component';
import { ErrorsService } from '../../../blocks/services/errors.service';

@Component({
  selector: 'app-manifest-list',
  templateUrl: './manifest-list.component.html',
  styleUrls: ['./manifest-list.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ManifestListComponent {
  @Input() component: DWFComponent;

  constructor(private translate: TranslateService, private errorsService: ErrorsService) {}

  createField = (label: string, placeholder: string, hideLabel: boolean = undefined) => {
    return new FieldDefinition({
      type: MetaDataType.Text,
      label: label ? this.translate.instant(label) : '',
      placeholder: placeholder ? this.translate.instant(placeholder) : '',
      required: true,
      hideLabel: hideLabel,
    });
  };

  addField = (): void => {
    this.component.manifest.push(
      new Manifest({
        path: 'Publish\\' + this.component.name,
        fileSpec: '*',
        recursive: true,
      })
    );
  };

  deleteItem = (item: IManifest): void => {
    const index = this.component.manifest.findIndex((x) => x == item);
    if (index != -1) this.component.manifest.splice(index, 1);
  };

  deleteAllItems = (): void => {
    this.component.manifest = [];
  };
}
