import { ChangeDetectorRef, Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Data, Params, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { ToastrService } from 'ngx-toastr';
import { Subscription, combineLatest } from 'rxjs';
import { EnumDataSource, FieldDefinition, MetaDataType, Tab } from 'shared-code-lib';
import { Log } from '../../blocks/core/log';
import { ComponentType, Component as DWFComponent, IComponent } from '../../blocks/models/component';
import { ErrorsService } from '../../blocks/services/errors.service';
import { HydraApiService } from '../../blocks/services/hydra-api.service';
import { ApiResult, Control } from '../../blocks/services/models';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';

@Component({
  selector: 'app-admin-component',
  templateUrl: './admin-component.component.html',
  styleUrls: ['./admin-component.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AdminComponentComponent implements OnInit, OnDestroy {
  rightControls: Array<Control> = [];

  routeParams: Params;
  routeData: Data;

  saveButton: Control;
  cancelButton: Control;
  component: DWFComponent;

  fieldName: FieldDefinition;
  fieldVersion: FieldDefinition;
  fieldType: FieldDefinition;

  activeTab = 'manifest';
  tabs = [];

  get componentComponentType(): string {
    return `${this.component.componentType}`;
  }
  set componentComponentType(val: string) {
    this.component.componentType = Number(val);
  }

  subscriptions = new Subscription();

  constructor(
    private translate: TranslateService,
    private router: Router,
    private route: ActivatedRoute,
    private hydraApi: HydraApiService,
    private toastr: ToastrService,
    private errorsService: ErrorsService,
    private spinner: SpinnerStateService,
    public ngxSmartModalService: NgxSmartModalService,
    private cdr: ChangeDetectorRef
  ) {}

  ngOnInit() {
    this.createTabs();

    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        this.routeParams = params;
        this.routeData = data;
        if (this.routeData.type == 'create') {
          this.component = new DWFComponent({
            id: '',
            name: '',
            componentType: ComponentType.Website,
            manifest: [],
            nextReleaseVersion: '1.0.0',
            dateCreatedUTC: new Date(),
            dateModifiedUTC: new Date(),
          } as IComponent);
        } else {
          this.loadItems();
        }
      })
    );

    this.createButtons();
    this.createFields();
  }

  createTabs() {
    this.tabs.push(new Tab('manifest', this.translate.instant('admin-component.manifest')));

    this.tabs.push(new Tab('variables', this.translate.instant('admin-component.variables')));
  }

  activateTab = (view) => {
    this.activeTab = view;
  };

  private createButtons = () => {
    this.saveButton = new Control(this.onSave, 'Save', 'btn btn-primary btn-minwidth', '', '', 'btn', false);
    this.cancelButton = new Control(this.onCancel, 'Cancel', 'btn btn-light btn-minwidth', '', '', 'btn', false);
    this.rightControls.push(this.saveButton);
    this.rightControls.push(this.cancelButton);

    this.saveButton.setTranslation(this.translate.instant('Common.Save'));
    this.cancelButton.setTranslation(this.translate.instant('Common.Cancel'));
  };

  private createFields = () => {
    this.fieldName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-component.name'),
      placeholder: this.translate.instant('admin-component.name-placeholder'),
      required: true,
    });

    this.fieldVersion = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-component.version'),
      placeholder: this.translate.instant('admin-component.version-placeholder'),
      required: true,
    });

    this.fieldType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('admin-component.type'),
      placeholder: this.translate.instant('admin-component.type-placeholder'),
      datasource: new EnumDataSource(ComponentType, 'ComponentType', this.translate),
      required: true,
    });
  };

  CreateField = (label: string, required: boolean = false): FieldDefinition => {
    return new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant(label),
      placeholder: this.translate.instant(label),
      required: required,
    });
  };

  onSave = () => {
    if (this.component.id) {
      this.hydraApi.updateComponent(this.component, this.componentSaved, this.componentSaveFailed);
    } else {
      this.hydraApi.createComponent(this.component, this.componentSaved, this.componentSaveFailed);
    }

    this.spinner.showSavingSpinner();
  };

  componentSaved = (result: ApiResult) => {
    if (result.success) {
      this.toastr.success(this.translate.instant('admin-component.saved'));
      this.returnToGrid();
    } else {
      this.errorsService.showErrorsModal(result.errors, 'admin-component.validation-errors');
    }
    this.spinner.hide();
  };

  componentSaveFailed = (error: any) => {
    this.toastr.error(this.translate.instant('admin-component.save-failed'));
    Log.error('admin-component.save-failed', error);
    this.spinner.hide();
  };

  onCancel = () => {
    this.returnToGrid();
  };

  returnToGrid = () => {
    this.router.navigateByUrl(
      'product/settings/BambaDWFToolsApplication/BambaDWFToolsApplication-Upland.Bamba.DWFTools.IFXReleaseManager.Components.Folder'
    );
  };

  componentLoaded = (ApiResult: any): void => {
    if (this.routeData.type === 'duplicate') {
      ApiResult.object.name = `${this.translate.instant('Admin.CopyOf')} ${ApiResult.object.name}`;
      ApiResult.object.id = '';
    }
    this.component = new DWFComponent({ ...ApiResult.object });
    this.cdr.markForCheck();
    this.spinner.hide();
  };

  componentFailed = (error: any): void => {
    this.toastr.error(this.translate.instant('admin-component.load-failed'));
    Log.error('admin-component.load-failed', error);
    this.spinner.hide();
  };

  loadItems = (): void => {
    if (this.routeData.type === 'properties' || this.routeData.type === 'duplicate') {
      this.spinner.show(this.translate.instant('admin-component.loading-component'), document.body);
      this.hydraApi.getComponent(this.routeParams.componentId, this.componentLoaded, this.componentFailed);
    }
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
